import React from 'react';

export const useFocusOnChange = <T = HTMLElement>(forwardRef: React.RefObject<T> | undefined, hash: unknown) => {
    const ref = React.useRef<T>(null);

    const onChangeFocusRef = forwardRef || ref;

    React.useEffect(() => {
        if (onChangeFocusRef.current instanceof HTMLElement) {
            onChangeFocusRef.current.focus();
        }
    }, [onChangeFocusRef, hash]);

    return onChangeFocusRef;
};
