import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { addressFullSubmitAction } from 'store/actions/server';
import { noop } from 'tools/noop';
import { Input } from 'components/Input/Input';
import { Button, ButtonViews } from 'components/Button/Button';

const StyledContainer = styled.div`
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const StyledFirstRow = styled.div`
    grid-area: first-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1.4fr 1fr;
    color: #fff;
`;

const StyledSecondRow = styled.div`
    grid-area: second-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1fr 1fr 1fr;
    color: #fff;
`;

const StyledThirdRow = styled.div`
    grid-area: third-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1.4fr 1fr;
    color: #fff;
`;

const StyledFourthRow = styled.div`
    grid-area: fourth-row;
`;

const SubmitButton = styled(Button)`
    flex-grow: 0;
    padding: 36px 44px;
`;

const StyledRoot = styled.div`
    display: grid;
    padding: 0 130px;
    box-sizing: border-box;
    grid-row-gap: 80px;
    grid-column-gap: 32px;
    grid-template-areas: 'first-row' 'second-row' 'third-row' 'fourth-row';

    will-change: transform;
`;

export const AddressFullAll: React.FC = () => {
    const dispatch = useDispatch();
    const {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
        nextFocus,
    } = useSelector(({ addressFullPage }) => addressFullPage);
    const handleSubmitClick = React.useCallback(() => {
        dispatch(addressFullSubmitAction());
    }, [dispatch]);

    return (
        <StyledContainer>
            <StyledRoot>
                <StyledFirstRow>
                    <Input
                        title="Улица и дом"
                        disabled
                        onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                        onRootKeyPress={() => dispatch(push(ROUTES.ADDRESS_SIMPLE))}
                        value={addressInputValue}
                    />
                    <Input
                        title="Номер телефона"
                        shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_PHONE}
                        onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                        onRootKeyPress={() => dispatch(push(ROUTES.ADDRESS_FULL_PHONE))}
                        value={phoneNumberInputValue}
                    />
                </StyledFirstRow>
                <StyledSecondRow>
                    <Input
                        title="Квартира"
                        shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_APARTMENT}
                        onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                        onRootKeyPress={() => dispatch(push(ROUTES.ADDRESS_FULL_APARTMENT))}
                        value={apartmentInputValue}
                    />
                    <Input
                        title="Подъезд"
                        shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_ENTRANCE}
                        onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                        onRootKeyPress={() => dispatch(push(ROUTES.ADDRESS_FULL_ENTRANCE))}
                        value={entranceInputValue}
                    />
                    <Input
                        title="Этаж"
                        shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_FLOOR}
                        onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                        onRootKeyPress={() => dispatch(push(ROUTES.ADDRESS_FULL_FLOOR))}
                        value={floorInputValue}
                    />
                </StyledSecondRow>
                <StyledThirdRow>
                    <Input
                        title="Комментарий"
                        shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_DOORCODE}
                        onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                        onRootKeyPress={() => dispatch(push(ROUTES.ADDRESS_FULL_DOORCODE))}
                        value={doorCodeInputValue}
                    />
                </StyledThirdRow>
                <StyledFourthRow>
                    <SubmitButton
                        view={ButtonViews.primary}
                        shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_ALL}
                        disabled={isSubmitButtonDisabled}
                        onClick={handleSubmitClick}
                    >
                        Сохранить
                    </SubmitButton>
                </StyledFourthRow>
            </StyledRoot>
        </StyledContainer>
    );
};
